import { DOCUMENT } from "@angular/common";
import { Component, Inject, Input, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { ModalController, Platform } from "@ionic/angular";
import { AjaxService } from "src/app/services/ajax.service";
import { CommonService } from "src/app/services/common.service";
import { serverUrl } from "src/environments/environment";

@Component({
  selector: "app-certificate-creation",
  templateUrl: "./certificate-creation.component.html",
  styleUrls: ["./certificate-creation.component.scss"],
})
export class CertificateCreationComponent implements OnInit {
  certificatedetails: FormGroup;
  @Input() value: any;
  // TotalcertificateData: Object = {};
  imeiSearch: FormGroup;
  encodedData: any;
  Certificate: any;
  show = false;
  certificatedata: any;
  companyId = localStorage.getItem("userName");
  data;
  myPlatform: any;
  renewalcount: any;
  TotalcertificateData: any = {};
  Apmlogo =
    "data:image/png;base64,";
  pdfDate: any;
  panic: any;

  constructor(
    private formBuilder: FormBuilder,
    private platform: Platform,
    private modalController: ModalController,
    @Inject(DOCUMENT) private document: Document,
    private ajaxService: AjaxService,
    private commonService: CommonService
  ) {}
  public values;
  public level: "L" | "M" | "Q" | "H";
  public width: number;

  cancelBtn() {
    this.modalController.dismiss();
  }
  clear() {
    this.certificatedetails.patchValue({
      plateno: "",
      imei: "",
      engineno: "",
      chassisno: "",
      service: "",
      panicCount: "0",
    });
  }
  creatFrom() {
    this.certificatedetails = this.formBuilder.group({
      plateno: ["", Validators.required],
      imei: ["", Validators.required],
      engineno: ["", Validators.required],
      chassisno: ["", Validators.required],
      service: ["", Validators.required],
      panicCount: ["0"],
    });
  }

  getdetails() {
    this.certificatedetails.patchValue({
      plateno: this.value.plateno,
      imei: this.value.imei,
      engineno: this.value.engineno,
      chassisno: this.value.chassisno,
    });
  }
  submit() {
    var data = {
      imei: this.certificatedetails.value.imei,
      engineno: this.certificatedetails.value.engineno,
      chassisno: this.certificatedetails.value.chassisno,
      createdby: localStorage.getItem("userName"),
      serviceengineer: this.certificatedetails.value.service,
      panicCount:
        this.certificatedetails.value.panicCount == ""
          ? "0"
          : this.certificatedetails.value.panicCount,
    };
    const url = serverUrl.web + "/global/saveCertificateDetails";
    this.ajaxService.ajaxPostWithBody(url, data).subscribe((res) => {
      if (res.message == "Certificate Saved Successfully") {
        this.commonService.showConfirmcer(res.message);
        this.TotalcertificateData = JSON.parse(res.certificatedetails);
        this.panic = JSON.parse(this.TotalcertificateData.paniccount);
        var detailofqrcode = [];
        detailofqrcode.push(
          this.TotalcertificateData.CenterName,
          "Chassis:" + this.TotalcertificateData.ChassisNo,
          "EngineNo:" + this.TotalcertificateData.EngineNo,
          "Reg.No:" + this.TotalcertificateData.RegistraionNo
        );
        this.data = detailofqrcode.toString();
        this.values = this.data;
        this.show = true;

        setTimeout(() => {
          this.clickToDownload();
        }, 1000);

        // this.modalController.dismiss({
        //   data: "Certificate Saved Successfully",
        // });
        // this.clear();
        // this.cancelBtn();
      } else {
        this.commonService.showConfirm(res.message);
      }
    });
  }

  clickToDownload() {
    // this.generatedcertificate();

    var printWindow = window.open("height=1000,width=900");
    printWindow.document.write("<html>");
    // printWindow.document.write("<body>");
    printWindow.document.write(
      '<body style="background-image: url(https://kingstrackimages.s3.amazonaws.com/Company/apmlogo_watermarker.svg);background-repeat: no-repeat;background-size: 100% 85%;">'
    );
    var divContents = document.getElementById("pdf2").innerHTML;
    printWindow.document.write(divContents);
    printWindow.document.write("</body></html>");
    printWindow.print();
    printWindow.document.close();
  }
  ionViewWillEnter() {
    const style = this.document.createElement("style");
    style.innerHTML = `
      @page {
        size: auto;
        margin: 0;
        /* Reset print settings */
        marks: none;
        widows: 0;
        orphans: 0;
      }
    `;
    this.document.head.appendChild(style);
  }

  ngOnInit() {
    this.creatFrom();
    if (Object.keys(this.value).length != 0) {
      console.log(this.value);
      this.getdetails();
    } else {
      // this.clear();
      this.value = {};
    }
  }
}
